.login-outer {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: cover;
  background-image: url("../../../assets/img/background.jpg");
}

.lang {
  position: absolute;
  top: 12px;
  right: 16px;
}

.card-login {
  border-radius: 40px;
  background-repeat: no-repeat;
  background-position: bottom left;
  background-size: 390px;
  box-shadow: 5px 6px 17px 0 rgba(0, 0, 0, 0.11);
  background-color: #ffffffbf;
}

.btn {
  border-radius: 7px;
  height: 38px;
  width: 85%;
  margin-top: 23px;
  background-color: #fd6400;
  box-shadow: none;
}

.card-body {
  margin: 0 auto;
  width: 100%;
}

.w-75 {
  width: 30% !important;
}

.info {
  color: #3e3e3e;
  font-family: "GothamNarrowBold", sans-serif;
  font-size: 12px;
  font-weight: bold;
}

.icon {
  position: relative;
  top: -7px;
  width: 11px;
}

.login-outer .mat-form-field-wrapper {
  padding-bottom: 4px;
}

.example-form {
  min-width: 150px;
  max-width: 500px;
  width: 100%;
}

.example-full-width {
  width: 100%;
}

.inputBtn input {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 0.7rem;
  background: #ffffff;
  font-weight: bold;
}

.Btns {
  background: #ff6400;
  color: #ffffff;
  font-size: 12px;
  font-weight: bold;
  width: 80%;
  margin: 0 auto;
}

.mat-form-field-appearance-outline .mat-form-field-wrapper {
  width: 80%;
  margin: 0 auto;
}

.textpart {
  text-align: center;
  font-weight: bold;
  font-size: 0.9rem;
  padding: 1rem 2.5rem 0 2.5rem;
  width: 80%;
  margin: 0 auto;
  margin-bottom: 1rem;
}

.cncl {
  background: #fcfdfd;
  color: #282626;
  font-size: 12px;
  font-weight: bold;
  width: 80%;
  margin: 0 auto;
  border: 1px solid #ccc;
  margin-bottom: 1.5rem;
}

.errpart {
  color: black;
  font-size: 13px;
  font-weight: bold;
  width: 90%;
  margin: 0 auto;
}

.errpartone {
  color: red;
  font-size: 13px;
  font-weight: bold;
  width: 90%;
  margin: 0 auto;
}

.inputbox {
  text-align: center;
}

.inputbox form {
  width: 80%;
  margin: 0 auto;
}

.textpart .maticons {
  font-size: 16px;
  color: #3780c5;
}


@media (max-width: 1025px) {
  .card-login {
    background-size: 390px;
    box-shadow: none;
    background-color: transparent;
    border: none;
  }
}

@media only screen and (max-width: 550px) {
  .card-login {
    border-radius: 40px;
    width: 100%;
    background-repeat: no-repeat;
    background-position: bottom left;
    background-size: 390px;
    box-shadow: none;
    background-color: transparent;
    border: none;
  }
}

.loginPart {
  width: 100%;
}

::ng-deep .mat-tooltip {
  font-size: 13px;
  white-space: pre-wrap;
  color: gray !important;
  background-color: #fff;
  max-width: 400px !important;
  margin-left: 200px !important;
  box-shadow: 0 3px 10px rgb(0 0 0 / 0.2) !important;
  font-weight: bold;
}

/*profile css*/
.profilepart .inpt {
  width: 23%;
  border: none;
  margin-bottom: 1.5rem;
}

.profilepart {
  background: #f1eded9e;
  border-radius: 45px;
  padding: 4rem 1rem 4rem 4rem;
}

.profilepart input {
  padding: 0.3rem;
}

.profileBtn {
  width: 100%;
  text-align: center;
}

.profileBtn .cancelBtn {
  width: 23%;
  margin-left: -38px;
  color: grey;
  border: 1px solid grey;
}

.profileBtn .submitBtn {
  width: 23%;
  background-color: #fd6400;
  color: #fff;
  border: none;
}

.heading h5 {
  text-align: center;
  padding-bottom: 1rem;
}

.mat-select {
  padding: 0.3rem;
}